.background {
    height: 100vh;
    
    background:pink;

}

.graphcont {
    height:15em;
    width:15em;
    background:chocolate;
    border-bottom-right-radius: 1em;
    overflow: hidden;

}

.infostripovly {
height:3em;
width:16em;
}

.maincont {
    height:18em;
    width:16em;
    background: linear-gradient(
        45deg,
        rgb(247, 86, 153) 20%,
         rgb(82, 199, 228) 70%
      );
    border-radius: 1em;
    overflow: hidden;
  
}

.pushbutton {
    width:4em;
    height:2em;
    background:orange;
    border-radius: 1em;
    margin-right: 1.5em;
    outline: none;
}

@keyframes slimanim {
    from {
        width:25%;
        height:25%;
    }

    50% {
        width:10%;
        height:10%;
    }


    to {
        width: 25%;
        height:25%;
    }
}

.flexed {
    display:flex;
    align-items: center;
    text-align: center;
}

.flexed {
    display:flex;
}

.flexcenter {
    place-content: center;
    place-items: center;

}

.flexcol {
    display: flex;
    flex-direction: column;

}


.flexend {
    place-content: flex-end;


}


